
.tweets{
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  position: relative;
  top: 25vh;
}
.headers{
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  left: -100px;
  top: 10vh;
}
img{
  object-fit: cover;
  height: 250px;
  width: 250px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.tweet{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
.red{
  color: red;
  font-size: 50px;
}
.quotes {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  left: 25px;
  top: 11.5vh;
}
.quotes p{
  width: 300px;
  line-height: 1.4;
}
